*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin:40px
}
.calculator{
    width: 40%;
    color: #063102;
}
.heading h3{
    font-size: 25px;
    color: #0c8d00;
}
#message{
    font-size: 20px;
    color: #063102;
}
.input-group{
    margin:20px 0;
}
.input-group label{
    display: block;
}
.input-group input,
.input-group button,
.input-group select{
    width: 70%;
    padding: 12px;
    border: 1px solid #0c8d00;
    outline: none;
    border-radius: 5px;
}
.input-group button{
    background-color: #0c8d00;
    color: #fff;
}
.results{
    box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.09);
    width: 50%;
    margin: 10px;
    padding: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
}















